<template>
  <div id="app">
    <svg-icon
      v-for="svg in list"
      v-show="svg.name.includes(keyword)"
      :key="svg.name"
      :svg="svg"
      @view="view"
      @copy="copy"
      @optimize="optimize"
    />
    <div class="svg-search">
      <el-input
        size="small"
        placeholder="请输入内容"
        prefix-icon="el-icon-search"
        v-model="keyword"
      />
    </div>
    <el-dialog
      class="svg-view"
      :title="current.name"
      :visible.sync="show"
      width="500px"
    >
      <div class="svg-view__content">
        <div v-html="current.data" :style="{ '--color': color }"></div>
        <el-color-picker class="svg-view__picker" v-model="color" />
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="warning" @click="optimize">去除Fill</el-button>
        <el-button type="primary" @click="generate">生成代码</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import SvgIcon from "@/components/SvgIcon.vue";
import copyboard from "./copy";

export default {
  name: "app",
  components: {
    SvgIcon
  },
  data() {
    return {
      current: {},
      color: "",
      keyword: "",
      show: false,
      list: []
    };
  },
  created() {
    window.addEventListener(
      "message",
      ({ data: { command, list, result } }) => {
        if (command === "update") {
          this.list = list;
        } else if (command === "optimized") {
          if (result) this.$message.success("优化成功");
          else this.$message.error("优化失败");
        }
      }
    );
    if (window.vscode) {
      window.vscode.postMessage({
        command: "update"
      });
    } else {
      this.$message.error("vscode不存在");
    }
  },
  watch: {
    current(val) {
      this.color = val.color;
    }
  },
  methods: {
    view(svg) {
      this.current = svg;
      this.show = true;
    },
    copy(svg) {
      copyboard(svg.name)
        .then(() => {
          this.$message.success("复制成功");
        })
        .catch(() => {
          this.$message.error("复制失败");
        });
    },
    optimize(svg) {
      svg = svg instanceof MouseEvent ? this.current : svg;
      console.log(svg);
      this.$confirm("此操作将改变SVG文件，确定继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(async () => {
        if (window.vscode) {
          window.vscode.postMessage({
            command: "optimize",
            svg
          });
        } else {
          this.$message.error("vscode不存在");
        }
      });
    },
    generate() {
      copyboard(
        `<svg-icon icon-class="${this.current.name}" fill-color="${this.color}" />`
      )
        .then(() => {
          this.$message.success("复制成功");
        })
        .catch(() => {
          this.$message.error("复制失败");
        });
    }
  }
};
</script>

